*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.1;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* Create a root stacking context (for react) */
#root,
#__next {
  isolation: isolate;
}

body {
  font-family: var(--font-family);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  font-size: 1.75rem;
  margin-bottom: 0.1875rem;
}
@media (max-width: 37.5em) {
  h1 {
    font-size: 1.5rem;
  }
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-family: "Inter", sans-serif;
}

.container {
  max-width: min(69.375rem, 100% - 3.125rem);
  margin-inline: auto;
}

.card {
  background-color: var(--dark-card);
  color: var(--dark-text1);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 5px;
}
.card--facebook {
  border-top: 5px solid var(--facebook);
}
.card--twitter {
  border-top: 5px solid var(--twitter);
}
.card--instagram {
  border-top: 5px solid;
}
.card--youtube {
  border-top: 5px solid var(--youtube);
}
.card__handle {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 1.75rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__count {
  color: var(--dark-text2);
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  color: var(--dark-text1);
  text-transform: uppercase;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.toggle {
  border: none;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
@media (max-width: 37.5em) {
  .toggle {
    width: 100%;
    justify-content: space-between;
    border-top: 1px solid var(--dark-text1);
    padding-top: 0.9375rem;
  }
}
.toggle label {
  color: var(--dark-text1);
  font-size: 0.875rem;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  width: 1.25rem;
  height: 1.125rem;
}
.toggle input[type=radio]:first-child {
  margin-right: 5px;
}
.toggle__background {
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--toggle-bg-start), var(--toggle-bg-end));
  position: absolute;
  top: 0;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  display: block;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 0.15s ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 0.1875rem;
}

.header {
  margin-top: 2.25rem;
  margin-bottom: 2.875rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}